<script setup>
import { reactive, ref } from "vue";
// 输入用
const orderForm = reactive({
  orderNumber: "" + Math.ceil(Math.random() * 100000000),
  // orderNumber: "" + Math.ceil(Math.random(1, 1000) * 100000000),
  orderTime: 0,
  images: "",
  title: "遊戯王OCG",
  tax: "10",
  quantity: 1,
  price: 1100,
  Subtotal: 0, //合計
  fret: 1100, //运费
  auTotal: 0, // 総合計
  fig: false,
  nfig: false,
  Abs: false,
  tax10: 0,
  delRow: "",
});

// 图片操作
const closeImg = () => {
  orderForm.fig = !orderForm.fig;
};
// 编号操作
const closeNum = () => {
  orderForm.nfig = !orderForm.nfig;
};
// 切换操作
const Abss = () => {
  orderForm.Abs = !orderForm.Abs;
};
// 删除操作
const deleteRow = () => {
  newData.value.splice(orderForm.delRow, 1);
  calculate(); // 分离计算
};
// 清空操作
const clear = () => {
  newData.value = [];
  orderForm.Subtotal = 0;
  orderForm.auTotal = 0;
  orderForm.tax10 = 0;
};

// 添加用
const newData = ref([]);
// 测试列表最终列表
const orderList = reactive([
  {
    images: "https://m.media-amazon.com/images/I/61aW44yqKZL._AC_SY450_.jpg",
    title:
      "【年末セール】ポケモンカードゲーム ソード＆シールド 拡張パック パラダイムトリガー BOX",
    tax: "10%",
    quantity: "6",
    price: 1650,
  },
]);

//添加列表
const onAddItem = () => {
  if (orderForm.orderTime) {
    orderForm.orderTime = orderForm.orderTime.replace(/T/, "日 ");
    orderForm.orderTime = orderForm.orderTime.replace(/-{1}/, "年");
    orderForm.orderTime = orderForm.orderTime.replace(/-{1}/, "月");
  }

  // const [title,tax,quantity,price,Subtotal]=["biaoti","jiage","shui"]
  let nSubtotal = (orderForm.quantity * orderForm.price).toLocaleString(
    "ja-JP"
  );
  let prices = orderForm.price.toLocaleString("ja-JP");
  // orderForm.auTotal = 0;
  // let spacecheck = /\D/gi;
  console.log(prices);
  newData.value.push({
    images: orderForm.images,
    title: orderForm.title,
    tax: orderForm.tax + "%",
    quantity: orderForm.quantity,
    price: prices,
    Subtotal: nSubtotal,
  });
  calculate(); // 分离计算
  console.log(newData);

  //  onUpdated
};
const calculate = () => {
  orderForm.Subtotal = 0;
  var spacecheck = /\D/gi;

  for (const item of newData.value) {
    orderForm.Subtotal += parseInt(item.Subtotal.replace(spacecheck, ""));
    orderForm.auTotal = orderForm.Subtotal + orderForm.fret;
  }
  orderForm.tax10 = (orderForm.Subtotal * orderForm.tax) / 100;
  orderForm.Subtotal = orderForm.Subtotal.toLocaleString("ja-JP");
  orderForm.auTotal = orderForm.auTotal.toLocaleString("ja-JP");
};

const getRandomColor = () => {
  return "#" + Math.floor(Math.random() * 16777215).toString(16);
};
